import React from 'react';
import { Link } from 'react-router-dom';
import * as img from "../components/import";

const Home = () => {
    return (
        <>
            <div className='container f1'>
                <div className='block'>
                    <h3 className='text-center '>     </h3>
                    <hr />
                    <div className="logo-size">
                        <img src={img.reactlogo} alt="" />
                    </div>
                    <hr />
                    <h3 className='text-center'> </h3>
                    <ol className='rectangle'>
                        <li><Link to="/about"> </Link></li>
                        <li><Link to="/task1">-</Link></li>
                        <li><Link to="/task2"></Link></li>
                        <li><Link to="/task3"></Link></li>
                        <li><Link to="/task4"></Link></li>
                        <li><Link to="/task5"></Link></li>
                        <li><Link to="/task6">  </Link></li>
                    </ol>
                </div>
            </div>
        </>
    );
}

export default Home;