import React from "react";
import { NavLink, Link } from "react-router-dom";
import logo from "../../../public/icons/logo.svg";

const Navbar = () => {

  return (
    <nav>
      <div className="navbar">
        <Link className="navbar-logo" to="/">
          <img className="navbar-logo" src={logo} alt="" />
        </Link>
        <Link className="navbar-item" to="/"></Link>
        <Link className="navbar-item" to="/about"> </Link>
        <Link className="navbar-item" to="/task1"> 1</Link>
        <Link className="navbar-item" to="/task2"> 2</Link>
        <Link className="navbar-item" to="/task3"> 3</Link>
        <Link className="navbar-item" to="/task4"> 4</Link>
        <Link className="navbar-item" to="/task5"> 5</Link>
        <Link className="navbar-item" to="/task6"> 6</Link>
      </div>
    </nav>
  )
}

export default Navbar;